<template>
  <div>
    <audio
      v-if="audioSrc"
      :src="audioSrc"
      controls
      @ended="$emit('changeCondition', 'audioEnd')"
    ></audio>

  </div>
</template>

<script>
export default {
  props: {
    data: String
  },
  computed: {
    audioSrc () {
      if (this.data) {
        return require(`@/assets/${this.data}.mp3`)
      }
      return null
    }
  }
}
</script>

