<template>
  <div class="checkbox" :class="classesCheckbox">
    <div class="top" @click="tryToggleState">
      <div class="squad">
        <div class="circle"
             v-if="data.selected"
        ></div>
      </div>
      <div class="title" v-html="data.title"></div>
    </div>
    <div class="description" v-html="data.description" v-if="isTested">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: Object,
    isTested: Boolean
  },
  computed: {
    classesCheckbox () {
      if (this.isTested) {
        if (this.data.answer === this.data.selected) {
          return 'checkbox_success'
        } else {
          return 'checkbox_error'
        }
      }
      return ''
    }
  },
  methods: {
    tryToggleState () {
      if (this.isTested) return
      this.$emit('toggleState')
    }
  }
}
</script>

