import {
  CHOICE_BUTTONS,
  VOICE_ANSWER_END,
  VOICE_ANSWER_INTRO
} from '../constants'
import VoiceAnswerEnd from '../components/ThirdComponent/VoiceAnswerEnd'
import VoiceAnswerIntro from '../components/ThirdComponent/VoiceAnswerIntro'
import ChoiceButtons from '../components/ThirdComponent/ChoiceButtons'

export function ThirdComponentFactory (variable) {
  switch (variable.type) {
    case VOICE_ANSWER_INTRO: {
      return VoiceAnswerIntro
    }
    case VOICE_ANSWER_END: {
      return VoiceAnswerEnd
    }
    case CHOICE_BUTTONS: {
      return ChoiceButtons
    }
    default: return null
  }
}
