<template>
  <div class="choice-buttons">
    <template
      v-for="(c, index) in data"
    >
      <component

        v-if="!c.condition || state.indexOf(c.condition) + 1"

        :state="choiceIndex === index ? finalAnswer === c.answer ? finalAnswer ? 'success' : 'error' : 'default' : 'default'"
        :key="index"
        :is="componentFactory(c)"
        :data="c.data"
        :audio="c.audio"

        @doChoice="$emit('changeCondition', 'choiceDid', c.answer, index)"
        @changeCondition="state = state + $event"
      ></component>
    </template>

  </div>
</template>

<script>
import { BaseComponentFactory } from '../../services/baseComponentFactory'
import { SecondComponentFactory } from '../../services/secondComponentFactory'
import { ThirdComponentFactory } from '../../services/thirdComponentFactory'

export default {
  props: {
    data: Array,
    finalAnswer: Boolean,
    choiceIndex: Number
  },
  data () {
    return {
      state: ''
    }
  },
  methods: {
    componentFactory (variable) {
      return BaseComponentFactory(variable) || SecondComponentFactory(variable) || ThirdComponentFactory(variable)
    }
  }
}
</script>

<style lang="scss">
@import '../../assets/styles/variables.scss';

.choice-buttons {
  @include flex;
  width: 90%;
  margin: 0 auto;
  justify-content: space-between;
}
</style>
