<template>
  <div v-html="data" class="choice-description" :class="classStyle">
  </div>
</template>

<script>
export default {
  props: {
    data: String,
    finalAnswer: Boolean
  },
  computed: {
    classStyle () {
      return this.finalAnswer ? 'choice-description_success' : 'choice-description_error'
    }
  }
}
</script>

<style lang="scss">
.choice-description {
  background: rgba(255, 255, 255, 0.5);
  border: 2px solid #FF6954;
  box-sizing: border-box;
  box-shadow: 0px 1px 4px #A3BCDB;
  border-radius: 25px;
  padding: 15px;
  max-width: 90%;
  margin: 13px auto 22px;
  font-weight: 300;
  font-style: italic;

  &_success {
    border: 2px solid #4DDE08;
  }
  &_error {
    border: 2px solid #FF6954;
  }
}
</style>
