<template>
  <div id="app">
    <transition name="fade">
      <Illustration
        v-if="currentTestStage.image &&
        $store.state.needImage !== currentTestStage.imageHideCondition"
        :image="currentTestStage.image"
      ></Illustration>
    </transition>

    <component
      :is="component"
      :data="currentTestStage"
      :key="stage"

      @nextStage="nextStage"
    ></component>
  </div>
</template>

<script>
import ButtonComponent from './components/BaseComponents/Button.vue'
import TextComponent from './components/BaseComponents/TextComponent.vue'
import { TopComponentFactory } from './services/topComponentFactory'
import Illustration from './components/NotTypedComponents/Illustration'

export default {
  name: 'App',
  components: {
    Illustration,
    TextComponent,
    ButtonComponent
  },
  data () {
    return {
      stage: 0
    }
  },

  computed: {
    testData () {
      return this.$store.state.testData
    },
    currentTestStage () {
      return this.testData[this.stage]
    },
    component () {
      return TopComponentFactory(this.testData[this.stage])
    }
  },
  mounted () {
    const vh = window.innerHeight * 0.01
    document.documentElement.style.setProperty('--vh', `${vh}px`)

    window.addEventListener('resize', () => {
      const vh = window.innerHeight * 0.01
      document.documentElement.style.setProperty('--vh', `${vh}px`)
    })
  },

  methods: {
    nextStage () {
      this.stage++
      this.$store.state.needImage = ''
    }
  }
}
</script>

<style media="screen" lang="scss" src="./assets/styles/main.scss"></style>
