<template>
  <div v-if="show">
    <template
      v-for="(c, index) in data"
    >
      <component

        v-if="!c.condition || state.indexOf(c.condition) + 1"

        :key="index"
        :is="componentFactory(c)"
        :data="c.data"
        :audio="c.audio"

        :finalAnswer="answer"
        :choiceIndex="indexL"

        @nextStage="$emit('nextStage')"
        @changeCondition="changeCondition"
      ></component>
    </template>

  </div>
</template>

<script>
import { BaseComponentFactory } from '../../services/baseComponentFactory'
import { SecondComponentFactory } from '../../services/secondComponentFactory'
import { ThirdComponentFactory } from '../../services/thirdComponentFactory'

export default {
  props: {
    data: Array,
    delay: [String, Number]
  },
  data () {
    return {
      state: '',
      answer: null,
      indexL: null,
      show: false
    }
  },
  methods: {
    componentFactory (variable) {
      return BaseComponentFactory(variable) || SecondComponentFactory(variable) || ThirdComponentFactory(variable)
    },
    changeCondition (state, answer, index) {
      if (this.state) return
      this.state = state
      this.answer = answer
      this.indexL = index
      this.$emit('changeCondition', 'testEnded')
    }
  },
  mounted () {
    if (this.delay) {
      setTimeout(() => {
        this.show = true
        this.$store.state.needImage = 'delay-ended'
      }, +this.delay)
    } else this.show = true
  }
}
</script>
