<template>

  <transiton name="fade" class="voice-answer">
    <div v-if="show" class="voice-answer">
      <template
        v-for="(c, index) in data"
      >
        <component
          v-if="!c.condition || c.condition === state"

          :key="index"
          :is="componentFactory(c)"
          :data="c.data"
          :audio="c.audio"

          @changeCondition="state = $event"
          @nextStage="$emit('nextStage')"
        ></component>
      </template>
    </div>
  </transiton>

</template>

<script>
import { BaseComponentFactory } from '../../services/baseComponentFactory'
import { SecondComponentFactory } from '../../services/secondComponentFactory'
import { ThirdComponentFactory } from '../../services/thirdComponentFactory'

export default {
  props: {
    data: Array,
    delay: [String, Number]
  },
  data () {
    return {
      state: 'first',
      show: false
    }
  },
  methods: {
    componentFactory (variable) {
      return BaseComponentFactory(variable) ||
        SecondComponentFactory(variable) ||
        ThirdComponentFactory(variable)
    }
  },
  mounted () {
    if (this.delay) {
      setTimeout(() => {
        this.show = true
      }, +this.delay)
    } else this.show = true
  }
}
</script>

<style media="screen" scoped>
  .voice-answer{
    width: 100%
  }
</style>
