import {
  AUDIO_DESCRIPTION,
  END_DESCRIPTION,
  MANY_ANSWER_TEST,
  SINGLE_ANSWER_TEST,
  TWO_CHOICES_TEST,
  VOICE_ANSWER
} from '../constants'
import AudioDescription from '../components/SecondComponents/AudioDescription'
import VoiceAnswer from '../components/SecondComponents/VoiceAnswer'
import ManyAnswerTest from '../components/SecondComponents/ManyAnswerTest'
import TwoChoicesTest from '../components/SecondComponents/TwoChoicesTest'
import EndDescription from '../components/SecondComponents/EndDescription'
import SingleAnswerTest from '../components/SecondComponents/SingleAnswerTest'

export function SecondComponentFactory (variable) {
  switch (variable.type) {
    case AUDIO_DESCRIPTION: {
      return AudioDescription
    }
    case VOICE_ANSWER: {
      return VoiceAnswer
    }
    case MANY_ANSWER_TEST: {
      return ManyAnswerTest
    }
    case SINGLE_ANSWER_TEST: {
      return SingleAnswerTest
    }
    case TWO_CHOICES_TEST: {
      return TwoChoicesTest
    }
    case END_DESCRIPTION: {
      return EndDescription
    }
  }
}
