<template>
  <div class="many-answer-test" v-if="show">
    <h1 v-if="data.title" v-html="data.title"></h1>
    <NotTypedCheckbox
      v-for="answer in answers"
      :key="answer.title"
      :data="answer"
      :isTested="isTested"
      @toggleState="answer.selected = !answer.selected"
    ></NotTypedCheckbox>
    <Button class="button_many-answers" v-if="!isTested" data="Проверить" @nextStage="test"></Button>
    <Button class="button_many-answers" v-else data="Далее" @nextStage="$emit('nextStage')"></Button>
  </div>
</template>

<script>
import NotTypedCheckbox from '../NotTypedComponents/NotTypedCheckbox'
import Button from '../BaseComponents/Button'
export default {
  components: { Button, NotTypedCheckbox },
  props: {
    data: Object,
    delay: [String, Number]
  },
  data () {
    return {
      answers: [],
      isTested: false,
      show: false
    }
  },
  mounted () {
    this.answers = this.data.answers.map(i => ({ ...i, selected: false }))
    if (this.delay) {
      setTimeout(() => {
        this.show = true
        this.$store.state.needImage = 'delay-ended'
      }, +this.delay)
    } else this.show = true
  },
  methods: {
    test () {
      this.isTested = true
    }
  }
}
</script>

<style media="screen">
  .many-answer-test{
    margin-top: 24px;
    margin-bottom: 16px;
    width: 100%;
    background: linear-gradient(157.27deg, rgba(255, 255, 255, 0.06) -3.22%, rgba(255, 255, 255, 0) 99.02%), rgba(255, 255, 255, 0.2);
    box-shadow: inset 1px 1px 0px rgba(255, 255, 255, 0.6), inset 0px -2px 35px rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(32px);
    border-radius: 30px;
    padding: 19px 15px;
    height: auto;
  }

  h1{
    font-weight: normal;
    font-size: 1em;
  }
</style>
