import {
  AUDIO,
  BUTTON,
  CHOICE_BUTTON, CHOICE_DESCRIPTION,
  IMAGE,
  LEFT_MESSAGE,
  RIGHT_MESSAGE,
  TEXT,
  TEXT_WITH_AUDIO,
  VIDEO
} from '../constants'
import Text from '../components/BaseComponents/TextComponent'
import Video from '../components/BaseComponents/Video'
import Button from '../components/BaseComponents/Button'
import Audio from '../components/BaseComponents/Audio'
import Image from '../components/BaseComponents/Image'
import RightMessage from '../components/BaseComponents/RightMessage'
import LeftMessage from '../components/BaseComponents/LeftMessage'
import TextWithAudio from '../components/BaseComponents/TextWithAudio'
import ChoiceButton from '../components/BaseComponents/ChoiceButton'
import ChoiceDescription from '../components/BaseComponents/ChoiceDescription'

export function BaseComponentFactory (variable) {
  switch (variable.type) {
    case TEXT: {
      return Text
    }
    case VIDEO: {
      return Video
    }
    case BUTTON: {
      return Button
    }
    case AUDIO: {
      return Audio
    }
    case IMAGE: {
      return Image
    }
    case RIGHT_MESSAGE: {
      return RightMessage
    }
    case LEFT_MESSAGE: {
      return LeftMessage
    }
    case TEXT_WITH_AUDIO: {
      return TextWithAudio
    }
    case CHOICE_BUTTON: {
      return ChoiceButton
    }
    case CHOICE_DESCRIPTION: {
      return ChoiceDescription
    }
    default: return null
  }
}
