<template>
  <div class="checkbox" :class="classesCheckbox">
    <div class="top" @click="tryToggleState">
      <div class="squad">
        <div class="circle"
             v-if="selected"
        ></div>
      </div>
      <div class="title">{{ data.title }}</div>
    </div>
    <div class="description" v-if="isTested" v-html="data.description">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    data: Object,
    isTested: Boolean,
    selected: Boolean
  },
  computed: {
    classesCheckbox () {
      if (this.isTested) {
        if (this.data.answer === this.selected) {
          return 'checkbox_success'
        } else {
          return 'checkbox_error'
        }
      }
      return ''
    }
  },
  methods: {
    tryToggleState () {
      if (this.isTested) return
      this.$emit('toggleState')
    }
  }
}
</script>

