<template>
  <img class="illustration" :src="imageSrc" alt="">
</template>

<script>
export default {
  props: {
    image: String
  },
  computed: {
    imageSrc () {
      if (this.image) {
        return require(`@/assets/${this.image}.png`)
      }
      return null
    }
  }
}
</script>

<style lang="scss">
.illustration {
  position: absolute;
  bottom: 125px;
}
</style>
