<template>
  <div class="audio-description">
    <component
      v-for="(c, index) in data"
      :key="index"
      :is="componentFactory(c)"
      :data="c.data"
      :audio="c.audio"
    ></component>
  </div>
</template>

<script>
import { BaseComponentFactory } from '../../services/baseComponentFactory'
import { SecondComponentFactory } from '../../services/secondComponentFactory'

export default {
  props: {
    data: Array
  },
  methods: {
    componentFactory (variable) {
      return BaseComponentFactory(variable) || SecondComponentFactory(variable)
    }
  },
  mounted () {
    setTimeout(() => {
      this.$store.state.needImage = 'audio-description-ended'
    }, this.data.length * 1000)
  }
}
</script>

<style media="screen" scoped>
  .audio-description{
    width: 100%;
  }
</style>
