<template>
  <img :src="imageSrc" alt="" class="image-component">
</template>

<script>
export default {
  props: {
    data: String
  },
  computed: {
    imageSrc () {
      if (this.data) {
        return require(`@/assets/${this.data}.png`)
      }
      return null
    }
  }
}
</script>
<style media="screen">
  .image-component{
    width: 90%;
    margin: 0 5%;
  }
</style>
