<template>
  <div class="single-answer-test" v-if="show">
    <h1 v-html="data.title"></h1>
    <NotTypedRadio
      v-for="(answer, index) in answers"
      :key="answer.title"
      :data="answer"
      :isTested="isTested"
      @toggleState="selected = index"
      :selected="selected === index"
    ></NotTypedRadio>
    <Button v-if="!isTested" data="Проверить" @nextStage="test"></Button>
    <Button v-else data="Далее" @nextStage="$emit('nextStage')"></Button>
  </div>
</template>

<script>
import Button from '../BaseComponents/Button'
import NotTypedRadio from '../NotTypedComponents/NotTypedRadio'
export default {
  components: { NotTypedRadio, Button },
  props: {
    data: Object,
    delay: [String, Number]
  },
  data () {
    return {
      answers: [],
      isTested: false,
      selected: null,
      show: false
    }
  },
  mounted () {
    this.answers = this.data.answers

    if (this.delay) {
      setTimeout(() => {
        this.show = true
        this.$store.state.needImage = 'delay-ended'
      }, +this.delay)
    } else this.show = true
  },
  methods: {
    test () {
      this.isTested = true
    }
  }
}
</script>
