.single-answer-test{
  margin: 24px auto 16px;
  width: 100%;
  background: linear-gradient(157.27deg, rgba(255, 255, 255, 0.06) -3.22%, rgba(255, 255, 255, 0) 99.02%), rgba(255, 255, 255, 0.2);
  box-shadow: inset 1px 1px 0px rgba(255, 255, 255, 0.6), inset 0px -2px 35px rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(32px);
  border-radius: 30px;
  padding: 19px 15px;
  height: auto;
}

h1{
  font-weight: normal;
  font-size: 1em;
}
</style>
