<template>
  <div class="voice-answer-intro">
      <component
        v-for="(c, index) in data"

        :key="index"
        :is="componentFactory(c)"
        :data="c.data"
        :audio="c.audio"

        text-default

        @nextStage="$emit('changeCondition', 'second')"
      ></component>
  </div>
</template>

<script>
import { BaseComponentFactory } from '../../services/baseComponentFactory'
import { SecondComponentFactory } from '../../services/secondComponentFactory'

export default {
  props: {
    data: Array
  },
  methods: {
    componentFactory (variable) {
      return BaseComponentFactory(variable) || SecondComponentFactory(variable)
    }
  }
}
</script>

