<template>
  <div class="task-container audio-voice-answer">
    <template
      v-for="(c, index) in data.content"
    >
      <component
        v-if="!c.condition || c.condition === state"

        :key="index"
        :is="componentFactory(c)"
        :data="c.data"
        :delay="c.delay"
        :audio="c.audio"

        @changeCondition="state = $event"
        @nextStage="$emit('nextStage')"
      ></component>
    </template>
  </div>
</template>

<script>
import { BaseComponentFactory } from '../../services/baseComponentFactory'
import { SecondComponentFactory } from '../../services/secondComponentFactory'

export default {
  props: {
    data: Object
  },
  data () {
    return {
      state: null
    }
  },
  methods: {
    componentFactory (variable) {
      return BaseComponentFactory(variable) || SecondComponentFactory(variable)
    }
  }

}
</script>

<style lang="scss">
.audio-voice-answer {
  height: 100%;
  width: 100%;

  justify-content: flex-start !important;
}
</style>
