<template>
  <div class="text-with-audio">
    <div v-html="data"></div>
    <audio
      v-if="audioSrc"
      :src="audioSrc"
      controls
      @ended="audioEnded"
    ></audio>
  </div>
</template>

<script>
export default {
  props: {
    data: String,
    audio: String
  },
  methods: {
    audioEnded () {
      this.$emit('changeCondition', 'audioEnd')
      this.$store.state.needImage = 'audio-ended'
    }
  },
  computed: {
    audioSrc () {
      if (this.audio) {
        return require(`@/assets/${this.audio}.mp3`)
      }
      return null
    }
  }
}
</script>

