.text-with-audio {
  max-width: 100%;
  padding: 19px 26px 15px;
  background-color: blue;
  background: linear-gradient(157.27deg, rgba(255, 255, 255, 0.06) -3.22%, rgba(255, 255, 255, 0) 99.02%), rgba(255, 255, 255, 0.2);
