import { AUDIO_TEST, AUDIO_VOICE_ANSWER, END, INTRO_VIDEO, INTRO_VIDEO_START, TEXT_MESSAGE_TEST } from '../constants'
import IntroVideo from '../components/TopComponents/IntroVideo'
import IntroVideoStart from '../components/TopComponents/IntroVideoStart'
import AudioTest from '../components/TopComponents/AudioTest'
import AudioVoiceAnswer from '../components/TopComponents/AudioVoiceAnswer'
import TextMessageTest from '../components/TopComponents/TextMessageTest'

export function TopComponentFactory (variable) {
  switch (variable.type) {
    case INTRO_VIDEO: {
      return IntroVideo
    }
    case INTRO_VIDEO_START: {
      return IntroVideoStart
    }
    case AUDIO_TEST: {
      return AudioTest
    }
    case AUDIO_VOICE_ANSWER: {
      return AudioVoiceAnswer
    }
    case TEXT_MESSAGE_TEST: {
      return TextMessageTest
    }
    case END: {
      return TextMessageTest
    }
    default: return null
  }
}
